.adalabers {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .adalabers__title {
    color: #099d8d;
    font-size: 24px;
    font-family: "Rubik";
    line-height: 45px;
    margin-bottom: 30px; }
    @media all and (min-width: 768px) {
      .adalabers__title {
        margin-top: 45px;
        margin-bottom: 45px;
        /*esto lo he metido nuevo*/ } }
  .adalabers__adalabersContainer {
    display: flex;
    flex-direction: column;
    align-items: center; }
    @media all and (min-width: 768px) {
      .adalabers__adalabersContainer {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        margin: 0 25px 60px 25px; } }
    @media all and (min-width: 768px) {
      .adalabers__adalabersContainer--even {
        flex-direction: row-reverse; } }

.adalabersContainer__girlImage {
  margin-top: 30px;
  margin-bottom: 15px;
  width: 180px;
  border: solid 5px #b8b8b9;
  border-radius: 2%; }
  @media all and (min-width: 768px) {
    .adalabersContainer__girlImage {
      width: 205px; } }

.adalabersContainer__girlText {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px; }
  @media all and (min-width: 768px) {
    .adalabersContainer__girlText {
      /*width: 439px;*/ } }
  @media all and (min-width: 768px) {
    .adalabersContainer__girlText--odd {
      align-items: flex-start; } }
  @media all and (min-width: 768px) {
    .adalabersContainer__girlText--even {
      align-items: flex-end; } }

.adalabersContainer__girlName {
  color: #000000;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  font-family: Open Sans; }
  @media all and (min-width: 768px) {
    .adalabersContainer__girlName {
      margin-top: 0px; } }

.adalabersContainer__girlDescription {
  color: #54585a;
  font-size: 16px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  line-height: 24px; }
  @media all and (min-width: 768px) {
    .adalabersContainer__girlDescription--odd {
      text-align: left; } }
  @media all and (min-width: 768px) {
    .adalabersContainer__girlDescription--even {
      text-align: right; } }

.adalabersContainer__icons {
  display: flex;
  width: 25%;
  margin-top: 15px;
  margin-bottom: 15px;
  font-size: 16px;
  justify-content: space-around; }
  .adalabersContainer__icons a {
    color: #099d8d; }

.adalabersContainer__dot {
  color: #54585a; }

/* FOOTER */
footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid #b8b8b9;
  padding-top: 30px;
  padding-bottom: 45px; }

.copy2020 {
  height: 20px;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: #b8b8b9; }

.menu-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  list-style-type: none;
  text-transform: uppercase;
  width: 114px;
  height: 60px;
  opacity: 0.75;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  text-align: center;
  color: #54585a;
  padding-top: 20px;
  padding-bottom: 30px; }

.team-1,
.team-1:visited,
.aboutus-1,
.aboutus-1:visited,
.contact-1,
.contact-1:visited {
  color: inherit;
  text-decoration: none; }

.logo {
  width: 115.9px;
  height: 45px; }

