    display: flex;
    flex-direction: row;
    height: 60px;
    align-items: center;
    padding: 0 45px 0 45px; }
  .headername {
    text-align: left; }
  .menu {
    height: auto;
    width: 80%; }
  .headermenu {
    padding: 0;
    justify-content: space-between;
    background: none; }
  /* ------------------------HEADER SECTION -ENDS----------------------*/
  /* ------------------------HERO SECTION -BEGINS----------------------*/
  .motto {
    margin: 0 115px 0 115px;
    top: -40px; }
  /* ------------------------HERO SECTION -ENDS----------------------*/
  .story {
    padding: 45px 45px 60px 45px; }
  .about-us-wrapper {
    display: flex;
    flex-direction: row; }
  .strengths {
    padding: 15px 15px 60px 45px;
    margin-top: -30px;
    flex: 1; }
  .weaknesses {
    padding: 15px 45px 60px 15px;
    margin-top: -30px;
    flex: 1; }
  /* ----------ABOUT US SECTION- ENDS------------ */
  /*---------------ADALABERS SECTION -START---------------*/
  .who-we-are-h1 {
    margin-top: 45px;
    line-height: 45px; }
  .girl-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin: 0 25px 60px 25px; }
  .girl-2,
  .girl-4 {
    flex-direction: row-reverse; }
  .girl-image {
    width: 205px; }
  .girl-text {
    width: 439px; }
  .girl-text-1,
  .girl-text-3,
  .girl-text-5 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    margin-top: 20px; }
  .girl-text-2,
  .girl-text-4 {
    align-items: flex-end; }
  .girl-name {
    margin-top: 5px;
    margin-bottom: 5px; }
  .girl-description-1,
  .girl-description-3,
  .girl-description-5 {
    text-align: left; }
  .girl-description-2,
  .girl-description-4 {
    text-align: right; }
  /*---------------ADALABERS SECTION - END---------------*/
  /*---------------FOOTER SECTION - START----------------*/
  footer {
    position: relative;
    align-items: start;
    padding: 0 0 60px 0; }
  .nav-footer {
    position: absolute;
    top: 0;
    right: 0; }
  .menu-footer {
    padding: 25px 45px 0 0;
    margin-top: 16px; }
  .copy2020 {
    padding: 30px 0 0 45px; }
  .logo {
    padding: 15px 0 0 45px; }
  .menu-footer li {
    display: block;
    padding: 8px 0; }
  /*------------FOOTER SECTION -ENDS-------------  */
  /*------------------------BREAKPOINT PARA DESKTOP----------------------*/ }

