* {
  padding: 0;
  margin: 0;
}

body {
  width: 100%;
  box-sizing: border-box;
}
/* ESTILOS HEADER */

.header {
  width: 100%;
  height: 105px;
  box-sizing: border-box;
  position: fixed;
  align-content: center;
  text-transform: uppercase;
  background: rgba(9, 157, 141, 0.95);
  opacity: 95%;
  z-index: 2;
}
/* NOMBRE EQUIPO */
.headername {
  width: 100%;
  height: 60px;
  text-align: center;
  opacity: 95%;
}
/* MENÚ */
.teamname {
  line-height: 30px;
  font-family: "rubik", sans-serif;
  font-size: 16px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  color: white;
  padding-top: 15px;
}
.menu {
  display: block;
  width: 100vw;
  height: 45px;
}
.headermenu {
  display: flex;
  flex-direction: row;
  justify-items: center;
  justify-content: space-evenly;
  text-align: center;
  padding-left: 16px;
  padding-right: 17px;
  padding-top: 8px;
  padding-bottom: 7px;
  list-style: none;
  background-color: #14d9c4;
}
/* LINKS MENÚ */
.team,
.aboutus,
.contact {
  line-height: 30px;
  font-family: "rubik", sans-serif;
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  text-decoration: none;
  color: white;
}
.team {
  width: 56px;
  height: 30px;
}
.aboutus {
  width: 121px;
  height: 30px;
}
.contact {
  width: 80px;
  height: 30px;
}
/* ESTILOS HERO */

.hero-image {
  width: 100vw;
  margin-top: 60px;
}

/* LEMA HERO */
.motto {
  position: relative;
  margin-left: 15px;
  margin-right: 15px;
  top: -30px;
  background-color: white;
}
.mottotext {
  padding: 15px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
}

/* ABOUT US */
.story,
.strengths,
.weaknesses,
.adalabers-container {
  padding: 30px 15px 30px 15px;
}

.story {
  margin-top: -66px;
  padding-top: 45px;
}

.strengths {
  padding-top: 0;
  padding-bottom: 0;
}

.weaknesses {
  padding-bottom: 45px;
}

.triangle-down {
  width: 0;
  height: 0;
  border-left: 31px solid transparent;
  border-right: 31px solid transparent;
  border-top: 31px solid white;
  margin: -1px auto;
}

.about-us-container {
  background-color: #b8b8b9;
}

.title {
  font-family: "Rubik";
  letter-spacing: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 45px;
  text-align: center;
  padding-bottom: 15px;
  font-weight: 500;
}

.text1,
.text2,
.text3 {
  font-family: "Open Sans";
  text-align: center;
}

.title1 {
  font-size: 24px;
  color: #14d9c4;
}

.title2,
.title3 {
  color: #099d8d;
  font-size: 22px;
}

