/* FOOTER */
footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid #b8b8b9;
  padding-top: 30px;
  padding-bottom: 45px;
}

.copy2020 {
  height: 20px;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: #b8b8b9;
}
.menu-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  list-style-type: none;
  text-transform: uppercase;
  width: 114px;
  height: 60px;
  opacity: 0.75;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  text-align: center;
  color: #54585a;
  padding-top: 20px;
  padding-bottom: 30px;
}

.team-1,
.team-1:visited,
.aboutus-1,
.aboutus-1:visited,
.contact-1,
.contact-1:visited {
  color: inherit;
  text-decoration: none;
}

.logo {
  width: 115.9px;
  height: 45px;
}

/* ------------------------MEDIA QUERIES----------------------
/*------------------------BREAKPOINT PARA TABLET----------------------*/

@media all and (min-width: 768px) {
  /* ------------------------HEADER SECTION -STARTS----------------------*/

  .header {
    display: flex;
    flex-direction: row;
    height: 60px;
    align-items: center;
    padding: 0 45px 0 45px;
  }

  .headername {
    text-align: left;
  }

  .menu {
    height: auto;
    width: 80%;
  }
  .headermenu {
    padding: 0;
    justify-content: space-between;
    background: none;
  }

  /* ------------------------HEADER SECTION -ENDS----------------------*/
  /* ------------------------HERO SECTION -BEGINS----------------------*/

  .motto {
    margin: 0 115px 0 115px;
    top: -40px;
  }

  /* ------------------------HERO SECTION -ENDS----------------------*/
  .story {
    padding: 45px 45px 60px 45px;
  }

  .about-us-wrapper {
    display: flex;
    flex-direction: row;
  }

  .strengths {
    padding: 15px 15px 60px 45px;
    margin-top: -30px;
    flex: 1;
  }

  .weaknesses {
    padding: 15px 45px 60px 15px;
    margin-top: -30px;
    flex: 1;
  }

  /* ----------ABOUT US SECTION- ENDS------------ */
  /*---------------ADALABERS SECTION -START---------------*/

  .who-we-are-h1 {
    margin-top: 45px;
    line-height: 45px;
  }
  .girl-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin: 0 25px 60px 25px;
  }

  .girl-2,
  .girl-4 {
    flex-direction: row-reverse;
  }
  .girl-image {
    width: 205px;
  }

  .girl-text {
    width: 439px;
  }

  .girl-text-1,
  .girl-text-3,
  .girl-text-5 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    margin-top: 20px;
  }

  .girl-text-2,
  .girl-text-4 {
    align-items: flex-end;
  }

  .girl-name {
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .girl-description-1,
  .girl-description-3,
  .girl-description-5 {
    text-align: left;
  }

  .girl-description-2,
  .girl-description-4 {
    text-align: right;
  }
  /*---------------ADALABERS SECTION - END---------------*/

  /*---------------FOOTER SECTION - START----------------*/
  footer {
    position: relative;
    align-items: start;
    padding: 0 0 60px 0;
  }

  .nav-footer {
    position: absolute;
    top: 0;
    right: 0;
  }

  .menu-footer {
    padding: 25px 45px 0 0;
    margin-top: 16px;
  }

  .copy2020 {
    padding: 30px 0 0 45px;
  }

  .logo {
    padding: 15px 0 0 45px;
  }
  .menu-footer li {
    display: block;
    padding: 8px 0;
  }
  /*------------FOOTER SECTION -ENDS-------------  */

  /*------------------------BREAKPOINT PARA DESKTOP----------------------*/

  @media all and (min-width: 1200px) {
    /* ------------HEADER SECTION START ------------ */
    .header {
      padding: 0 138px 0 138px;
    }

    .menu {
      width: 50%;
    }

    .motto {
      margin: 0 138px 0 138px;
      top: -60px;
    }

    /* ------------ABOUT US SECTION - START ------------ */
    .story {
      padding: 45px 300px 60px 300px;
    }

    .about-us-container {
      margin: auto 138px;
    }

    .strengths {
      padding: 15px 30px 60px 138px;
    }

    .weaknesses {
      padding: 15px 138px 60px 30px;
    }

    /*---------------ADALABERS SECTION - START---------------*/

    /*---------------ADALABERS SECTION - ENDS---------------*/

    /* ----------FOOTER SECTION - STARTS----------- */
    .menu-footer {
      padding: 25px 138px 0 0;
    }

    .copy2020 {
      padding: 30px 0 0 138px;
    }

    .logo {
      padding: 15px 0 0 138px;
    }
  }
  /* ----------FOOTER SECTION - ENDS----------- */
}
